@import "styles.scss";

.color-input {
  display: flex;
  position: relative;
  min-height: 32px;
  border-radius: 2px;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  overflow: hidden;
  flex-direction: row;
  flex: 0 0 auto;
}

.color-input span {
  width: 32px;
  margin-right: 1px;
}

.color-input div {
  display: flex;
  padding: 0 12px;
  border: 1px solid $clr__gray;
  background-color: $clr__white;
  justify-content: center;
  align-items: center;
  flex: 1 0 auto;
}

.color-input_checked div {
  border-color: $clr__lightgreen;
  background-color: $clr__lightgreen;
  color: $clr__white;
}

.color-input:not([class*="checked"]) div:hover {
  animation: hoverAnimation 0.4s infinite alternate;
}
